$("._tab").click(function() {
    let parentBlock = $(this).parents("._tabs-parent")
    let tabId = $(this).attr("data-tab")
    $(parentBlock).find("._tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.${tabId}`).addClass("_active")
})

$("._show-all-partners").click(function () {
    $(this).parents(".partners__nav").siblings(".partners__items").addClass("_show-all")
})

$(".licensy-card").click(function () {
    let imgSrc = $(this).attr("data-img")
    let title = $(this).attr("data-title")

    $(".licensy-modal .licensy-card__img img").attr("src",imgSrc)
    $(".licensy-modal .licensy-card__text .text14").html(title)
    openModal("licensyModal")
})


$(".structure-card2__top").click(function () {
    $(this).parents(".structure-card2").toggleClass("_active")
    $(this).siblings(".structure-card2__hidden").slideToggle()
})

$(".qa-card__show").click(function () {
    if($(this).parents(".qa-card").hasClass("_active")){
        $(".qa-card").removeClass("_active")
        $(".qa-card__hidden").slideUp()
    } else{
        $(".qa-card").removeClass("_active")
        $(".qa-card__hidden").slideUp()
        $(this).parents(".qa-card").addClass("_active")
        $(this).siblings(".qa-card__hidden").slideDown()
    }
})

$(".order-address__hint").click(function () {
    $(".order-hint").toggleClass("_active")
})

$(".order-hint .close-btn").click(function () {
    $(".order-hint").removeClass("_active")
})

$(document).mouseup( function(e){
    let div = $( ".order-hint" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_active")){
            $(div).removeClass("_active")
        }
    }
});

$('.cartcalc').each(function (i,el) {
    let plus = $(el).find(".ccalc-plus")
    let minus = $(el).find(".ccalc-minus")
    let input = $(el).find("input")

    $(minus).click(function(e) {
        let a = $(input).val();
        if (a > 1) {
            let b = +a - 1;
            $(input).val(b);
            $(input).trigger('change');
        } else {
            $(input).val(a);
        }
        setMinusBtnClickability()
        e.stopImmediatePropagation()
    });

    $(plus).click(function(e) {
        let a = $(input).val();
        let b = +a + 1;
        $(input).val(b);
        $(input).trigger('change');
        setMinusBtnClickability(this)
        e.stopImmediatePropagation()
    });

    function setMinusBtnClickability() {
        let val = +$(input).val()
        if(val === 1){
            $(minus).addClass("_disabled")
        } else{
            $(minus).removeClass("_disabled")
        }
    }

    setMinusBtnClickability()
})
